	const app = getApp()
	import helper from '../common/helper.js';
export default {
	data() {
		return {
			index: '0',
			payType: 'wechat',
			money: ''
		};
	},
	onLoad(e) {
		this.userInfo = app.globalData.userInfo
		console.log(this.userInfo)
		console.log(e)
		this.money = e
	},
	methods: {
		radioChange(e) {
			console.log(e);
			this.payType = e.detail.value;
		},
		wxAppletPay() {
			// 微信小程序支付
			uni.showLoading({
			  title: '支付信息加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/login`,
				method: 'POST',
				data: {
					username: loginName,
					password: loginPwd
				},
				success: res => {
					console.log(res);
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.statusCode == 200) {
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 2000
						});
					}
				}
			});
		},
		iAgree() {
			console.log(this.payType);
			uni.request({
				url: `${helper.requestUrl}/bay-vip`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data:{
					member_id: this.money.id,
					pay_type: this.payType
				},
				success: res => {
					console.log(res);
					console.log(res.data)
					// 调起支付
					uni.requestPayment({
						provider: 'alipay',
						orderInfo: res.data, //订单数据
						success: function(res) {
							console.log('success:' + JSON.stringify(res));
							console.log('*********')
						},
						fail: function(err) {
							// console.log('fail:' + JSON.stringify(err));
						}
					});
				}
			});
		}
	}
};
